.signup {
    position: fixed;
    left: 0;
    top: 0;
    background-color: rgba(0, 0, 0, 0.6);
    height: 100vh;
    width: 100vw;
    z-index: 999;
}

.signup p {
    color: #575368;
    font-family: Poppins;
    font-size: 12px;
    font-weight: 400;
}

.signup h1 {
    color: #0f0a28;
    font-family: Poppins;
    font-size: 24px;
    font-weight: 600;
}

.signup .bg {
    width: 100%;
    height: 100%;
}
  
.signup .container {
    width: 750px;
    height: 550px;
    box-shadow: 0 3px 6px rgba(0, 0, 0, 0.16);
    border-radius: 8px 8px 8px 8px;
    background-color: #ffffff;
    overflow: hidden;
    text-align: center;
    padding-top: 32px;

    margin: 0;
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);

    -webkit-animation: slide-in .4s .0s backwards;
    animation: slide-in .4s .0s backwards;
}

@-webkit-keyframes slide-in {
    0% {
        opacity: 0;
        -webkit-transform: translate(-50%, 0);
        transform: translate(-50%, 0);
    }
    100% {
        opacity: 1;
        -webkit-transform: translate(-50%, -50%);
                transform: translate(-50%, -50%);
    }
}

@keyframes slide-in {
    0% {
        opacity: 0;
        -webkit-transform: translate(-50%, 0);
        transform: translate(-50%, 0);
    }
    100% {
        opacity: 1;
        -webkit-transform: translate(-50%, -50%);
                transform: translate(-50%, -50%);
    }
}

